<template>
  <div class="row">
    <div class="col-md-4 offset-md-4 floating-box">
      <Message :show.sync="msgShow" :type="msgType" :msg="msg"/>
      <div class="panel panel-default">
        <div class="panel-heading">
          <h3 class="panel-title"></h3>
        </div>

        <div class="panel-body" data-validator-form>
          <div class="form-group">
            <label class="control-label"></label>
            <input v-model.trim="username" v-validator:input.required="{ regex: /^[a-zA-Z]+\w*\s?\w*$/, error: '' }" type="text" class="form-control" placeholder="">
          </div>
          <div class="form-group">
            <label class="control-label"></label>
            <input id="password" v-model.trim="password" v-validator.required="{ regex: /^\w{6,16}$/, error: ' 6 ~ 16 ' }" type="password" class="form-control" placeholder="">
          </div>
          <div class="form-group">
            <label class="control-label"></label>
            <input v-model.trim="cpassword" v-validator.required="{ target: '#password' }" type="password" class="form-control" placeholder="">
          </div>
          <div class="form-group">
            <label class="control-label"></label>
            <input v-model.trim="captcha" v-validator.required="{ title: '' }" type="text" class="form-control" placeholder="">
          </div>
          <div class="thumbnail" title="" @click="getCaptcha">
            <div class="captcha vcenter" v-html="captchaTpl"></div>
          </div>
          <button type="submit" class="btn btn-lg btn-primary btn-block" @click="register">
            <i class="fa fa-btn fa-sign-in"></i> 
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import createCaptcha from '@/utils/createCaptcha'
import ls from '@/utils/localStorage'

export default {
  name: 'Registered',
  data() {
    return {
      captchaTpl: '', // 
      username: '', // 
      password: '', // 
      cpassword: '', // 
      captcha: '', // 
      msg: '', // 
      msgType: '', // 
      msgShow: false // 
    }
  },
  created() {
    this.getCaptcha()
  },
  methods: {
    getCaptcha() {
      const { tpl, captcha } = createCaptcha(6)

      this.captchaTpl = tpl
      this.localCaptcha = captcha
    },
    register(e) {
      this.$nextTick(() => {
        const target = e.target.type === 'submit' ? e.target : e.target.parentElement

        if (target.canSubmit) {
          this.submit()
        }
      })
    },
    submit() {
      if (this.captcha.toUpperCase() !== this.localCaptcha) {
        this.showMsg('')
        this.getCaptcha()
      } else {
        const user = {
          name: this.username,
          password: this.password,
          avatar: `https://api.adorable.io/avatars/200/${this.username}.png`
        }
        // const localUser = ls.getItem('user')
        const localUser = this.$store.state.user
        if (localUser) {
          if (localUser.name === user.name) {
            this.showMsg('')
          } else {
            this.login(user)
          }
        } else {
          this.login(user)
        }
      }
    },
    login(user) {
      // ls.setItem('user', user)
      this.$store.dispatch('login',user)
      this.showMsg('', 'success')
    },
    showMsg(msg, type = 'warning') {
      this.msg = msg
      this.msgType = type
      this.msgShow = false

      this.$nextTick(() => {
        this.msgShow = true
      })
    }
  }
}
</script>

<style scoped>
.thumbnail { width: 170px; margin-top: 10px;margin-bottom: 10px; cursor: pointer;}
.thumbnail .captcha { height: 46px; background: #E1E6E8;}
.captcha{font-size:24px; font-weight:bold;user-select: none;}
</style>


